import { useMemo, useState } from "react";
import { useRecoilValue } from "recoil";
import styled from "styled-components";
import { IToDo } from "../../interface";
import { paging } from "../../store";
import { sortList } from "../../util/sort";
import ToDoCard from "./ToDoCard";

const Wrapper = styled.div`
  max-width: 660px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 10px;
`;

const NoToDo = styled.div`
  font-size: 1.3em;
  font-family: "Times New Roman", Times, serif;
`;

interface ListProps {
  propList: IToDo[];
}

function ToDoList({ propList }: ListProps) {
  const page = useRecoilValue(paging);
  const sorting = useMemo(() => sortList(propList), [propList]);

  const list = sorting.slice(
    (page.nowPage - 1) * page.pageValue,
    page.nowPage * page.pageValue
  );

  return (
    <Wrapper>
      {list.length === 0 && page.nowPage === 1 ? (
        <NoToDo>  ToDo .</NoToDo>
      ) : (
        list.map((todo: IToDo) => (
          <ToDoCard
            key={todo.id}
            id={todo.id}
            text={todo.title}
            deadline={todo.deadline}
            done={todo.done}
          />
        ))
      )}
    </Wrapper>
  );
}

export default ToDoList;
