import { useForm } from "react-hook-form";
import { useRecoilState, useRecoilValue } from "recoil";
import { IToDo, IModal } from "../../interface";
import { updateFromLocalStorage } from "../../localStorage";
import { modalActive, toDos } from "../../store";
import {
  Container,
  Form,
  Title,
  UpdateDate,
  UpdateContent,
  Button,
} from "./update.style";

function UpdateBoard() {
  const [modal, setModal] = useRecoilState<IModal>(modalActive);
  const [toDoList, setToDoList] = useRecoilState(toDos);
  const { register, handleSubmit } = useForm<IToDo>();
  const localToDos = useRecoilValue<IToDo[]>(toDos);
  const toDo = localToDos.find((toDo) => toDo.id === modal.id);
  const exitModal = () => {
    setModal((prev) => {
      return {
        active: !prev.active,
        id: modal.id,
      };
    });
  };

  const onValid = (data: IToDo) => {
    const newToDo = {
      id: modal.id,
      title: data.title,
      deadline: data.deadline,
      done: false,
    };
    updateFromLocalStorage(newToDo);
    setToDoList(() => {
      return [...toDoList.filter((todo) => todo.id !== modal.id), newToDo];
    });
    alert(" ");
    exitModal();
  };
  return (
    <Container style={{ top: 100 }}>
      <Form onSubmit={handleSubmit(onValid)}>
        <Title>TODO </Title>
        <UpdateDate
          defaultValue={toDo?.deadline}
          type="date"
          {...register("deadline", {
            required: " .",
          })}
        />
        <UpdateContent
          defaultValue={toDo?.title}
          {...register("title", {
            required: " .",
            minLength: {
              value: 1,
              message: "   .",
            },
            maxLength: {
              value: 40,
              message: "40  .",
            },
          })}
        />
        <Button type="submit"></Button>
        <Button onClick={exitModal}></Button>
      </Form>
    </Container>
  );
}

export default UpdateBoard;
